<script context="module">
	import {cbc, bmp, listableLabs} from './lab_utils.js';
	import renderPatient from './templates/patient.js';
	import parsePatient from './epic_parser.js';
	import Patient from './Patient.js';

	let nextPatientId = 1;

	const MONTHS = [
		"Jan",
		"Feb",
		"Mar",
		"Apr",
		"May",
		"Jun",
		"Jul",
		"Aug",
		"Sep",
		"Oct",
		"Nov",
		"Dec"
	];

	function date(date) {
		let [wday, month, day, year] = date.split(" ");
		month = MONTHS.indexOf(month) + 1;
		return month.toString() + "/" + day;
	}
</script>

<script>
	let patients = [];

	function patientIndex(patientID) {
		return patients.findIndex(e => e.id == patientID);
	}

	function patientID(event) {
		return parseInt(event.target.closest('li').dataset.patientId);
	}

	const RENDER_PATIENT_HELPERS = {bmp, cbc, date, listableLabs};

	function print() {
		let rendered = '';
		for (let patient of patients) {
			rendered += renderPatient(patient, RENDER_PATIENT_HELPERS);
		}
		const patientList = document.querySelector('.printable-patient-list');
		patientList.innerHTML = rendered;
	}

	function addPatient(event) {
		const text = event.target.value;

		if (text.trim().length === 0) return;

		event.target.value = "";
		const newPatient = parsePatient(text);//new Patient();
		console.log(newPatient);
		newPatient.id = nextPatientId;
		patients = [...patients, newPatient];
		nextPatientId++;
	}

	function updatePatient(event) {
		const text = event.target.value;

		if (text.trim().length === 0) return;

		event.target.value = "";
		const i = patientIndex(patientID(event));
		const updatedPatients = patients.slice();
		updatedPatients[i].addDayEntry(parsePatient(text));
		patients = updatedPatients;
	}

	function removePatient(event) {
		const i = patientIndex(patientID(event));
		patients = [...patients.slice(0, i), ...patients.slice(i + 1)];
	}

	function toJSON() {
		JSON.stringify(patients.map(e => e.toJSON()));
	}

	function fromJSON(json) {
		patients = JSON.parse(json).map(e => new Patient(e));
	}

	function toURL() {
		const data = encodeURIComponent(toJSON());
		return window.location.pathname + "#" + data;
	}

	function fromURL(urlFragment) {
		fromJSON(decodeURIComponent(urlFragment));
	}

	function copyPermalink(event) {
		toURL();
	}

	function loadFromPermalink(event) {
		const fragment = window.location.hash;
		if (fragment.length > 0) {
			fromURL(fragment.slice(1));
		}
	}
</script>

